.footer {
  width: 100%;
  height: 419px;
  margin: 50px auto 0 auto;
  background-color: #e6e6e6;
}

.footer-content {
  width: 75%;
  margin: 0 auto;
  position: relative;
}
@media screen and (max-width:1200px) {
  .footer-content{
    width: 95%;
  }
  
}
.footer-content > div > img {
  position: absolute;
  top: 72px;
  left: 0;
}

.footer-contact {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
  margin-top: 50.27px;

  position: absolute;
  left: 0;
  top: 130px;
}

.footer-contact  table  tbody tr {
  margin: 15px;
}
.footer-contact td {
  width: 180px;
  text-align: left;
  font-size: 14px;
  margin-right: 40px;
}

.maker {
  position: absolute;
  width: max-content;
  margin: 0 auto;
  top: 300px;
  left: 40%;
  right: 40%;
}

#about > a{
  color: rgba(55, 58, 64, 1);
}
#phone_about > a{
  color: rgba(55, 58, 64, 1);
}

#table_phone,#table_phone2{
  display: none;
}
@media screen and (max-width: 700px) {
  .footer{
    width: 100%;
  }
  #table_desktop {
    display: none;
  }
  #table_desktop2 {
    display: none;
  }
  #table_phone {
    display: block;
  }
  #table_phone2 {
    display: block;
  }
  .footer {
    width: 100%;
    height: 642px;
    margin: 50px auto 0 auto;
    background-color: #e6e6e6;
  }
  .footer-content {
    width: 95%;
    margin: 0 auto;
    position: relative;
  }
  .footer-content > div > img {
    position: absolute;
    width: 147.6px;
    height: 40px;
    top: 72px;
    top: 60.5px;
    left: 15px;
  }

  .footer-contact {
    display: block;
    width: 100%;
    margin: 0 auto;

    position: absolute;
    left: 15px;
    top: 160.5px;
  }

  .footer-contact > #table_phone tbody tr,
  .footer-contact > #table_phone2  tbody tr {
    margin: 18px;
  }
  .footer-contact > #table_phone2 {
    margin-top: 60px;
    text-align: left;
    width: 100%;
    margin: 60px auto 0;
  }
  .footer-contact > #table_phone  tbody tr td {
    margin: 18px;
    width: 120px;
    font-size: 14px;
  }
  .footer-contact > #table_phone2  tbody tr td {
    font-size: 14px;
    margin: 18px;
  }

  .maker {
    position: absolute;
    width: max-content;
    margin: 0 auto;
    top: 500px;
    left: 10%;
    right: 10%;
  }
}
